<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader
            :title="headercontent.title"
            :addlink="headercontent.addlink"
          />
        </v-col>
        <v-col md="12" v-if="false">
          <h3 class="filtertitle">Filter {{ headercontent.title }}</h3>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="3">
                <v-text-field
                  v-model="title"
                  outlined
                  label="Category Titlte"
                  class="mr-btm-minus"
                ></v-text-field>
              </v-col>
              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                >
                  Filter {{ headercontent.title }}
                </v-btn>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
        <v-col md="12">
          <v-simple-table class="table" v-if="!loading">
            <template v-slot:default v-if="allChallenges.results">
              <thead>
                <tr>
                  <th class="text-left">S/N</th>
                  <th class="text-left">Challenge Name</th>
                  <th class="text-left">Image</th>
                  <th class="text-left">Status</th>
                  <th class="text-right" colspan="2">Edit</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in allChallenges.results" :key="index">
                  <td>{{ index + 1 }}</td>
                  <td class="text-capitalize">{{ item.challengeName }}</td>
                  <td class="text-capitalize">
                    <div class="imageBox">
                      <img :src="item.image" alt="" width="100" height="100" />
                    </div>
                  </td>
                  <td class="text-capitalize">
                    <v-switch
                      v-model="item.status"
                      @change="updateChallengeStatus(item._id, item.status)"
                    ></v-switch>
                  </td>

                  <td class="text-right">
                    <router-link
                      :to="`/dashboard/addchallenge/add/${item._id}`"
                    >
                      <v-icon class="icons-action" v-if="true">
                        mdi-pencil
                      </v-icon></router-link
                    >
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <div v-if="loading">
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
            <v-skeleton-loader type="image" height="200"></v-skeleton-loader>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Challange",
  components: { PageHeader },
  data: () => ({
    headercontent: {
      title: "Challanges",
      addlink: "/dashboard/addchallenge/add",
    },
    title: "",
    adsposition: "",
    valid: true,
    limit: 100,
    skip: 1,
    loading: true,
  }),
  methods: {
    validate() {},
    ...mapActions(["getChallenges", "postChallenge"]),
    async updateChallengeStatus(id, status) {
      await this.postChallenge({
        _id: id,
        status: status,
      });
      await this.getChallenges({ limit: this.limit, skip: this.skip });
    },
  },
  async created() {
    await this.getChallenges({ limit: this.limit, skip: this.skip });
    this.loading = false;
  },
  computed: {
    ...mapGetters(["allChallenges"]),
  },
};
</script>

